import React, { useState, useEffect } from "react";
import axios from "axios";
import { Card, Avatar, Pagination, Input, Select } from "antd";
import "bootstrap/dist/css/bootstrap.css";
import styles from "../css/style.css";

const { Search } = Input;
const { Option } = Select;

const Userdata = () => {
  const [users, setUsers] = useState([]);
  const [filteredUsers, setFilteredUsers] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchName, setSearchName] = useState("");
  const [selectedGender, setSelectedGender] = useState("all");
  const [selectedDomain, setSelectedDomain] = useState("all");
  const [selectedAvalible, setSelectedAvailable] = useState("all");
  const usersPerPage = 20;

  useEffect(() => {
    axios
      .get("http://localhost:5000/api/getUsers")
      .then((response) => {
        setUsers(response.data);
        setFilteredUsers(response.data);
      })
      .catch((err) => console.log(err));
  }, []);

  useEffect(() => {
    const filteredData = users.filter((user) => {
      const fullName = `${user.first_name} ${user.last_name}`;
      const nameMatch = fullName
        .toLowerCase()
        .includes(searchName.toLowerCase());
      const genderMatch =
        selectedGender === "all" || user.gender === selectedGender;
      const domainMatch =
        selectedDomain === "all" ||
        user.domain.toLowerCase() === selectedDomain.toLowerCase();
      const availableMatch =
        selectedAvalible === "all" || user.available === selectedAvalible;
      return nameMatch && genderMatch && domainMatch && availableMatch;
    });
    setFilteredUsers(filteredData);
  }, [users, searchName, selectedGender, selectedDomain, selectedAvalible]);

  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handleSearch = (value) => {
    setSearchName(value);
  };

  const handleGenderChange = (value) => {
    setSelectedGender(value);
  };

  const handleDomainChange = (value) => {
    setSelectedDomain(value);
  };

  const handleAvailblityChange = (value) => {
    setSelectedAvailable(value);
  };

  return (
    // <div className="back  " style={{ backgroundColor: "green" }}>
      <div
        className="container mt-2 "
        // style={{ padding: "5%x 5%", backgroundColor: "#B3EDE3" }}
      >
        <div className="row mb-3 ">
          <div className="col-md-4">
            <Search
              placeholder="Search by name"
              onChange={(e) => handleSearch(e.target.value)}
            />
          </div>
          <div className="col-md-4">
            <Select
              style={{ width: "50%" }}
              defaultValue="all"
              onChange={handleGenderChange}
            >
              <Option value="all">All Gender</Option>
              <Option value="Male">Male</Option>
              <Option value="Female">Female</Option>
            </Select>
          </div>
          <div className="col-md-4 ">
            <Select
              style={{ width: "50%" }}
              defaultValue="all"
              onChange={handleDomainChange}
              id="domain"
            >
              <Option value="all">All Domains</Option>
              <Option value="finance">Finance</Option>
              <Option value="Management">Management</Option>
              <Option value="Sales">Sales</Option>
              <Option value="UI Designing">UI Designing</Option>
              <Option value="IT">IT</Option>
              <Option value="Marketing">Marketing</Option>
            </Select>

            {/* </div>
        <div className="col-md-4 "> */}
            <Select
              style={{ width: "50%", padding: "0px 10px" }}
              defaultValue="all"
              onChange={handleAvailblityChange}
              id="available"
            >
              <Option value="all">check availability</Option>
              <option value={users.available ? true : true}> Available</option>
              <option value={users.available ? false : false}>
                Not Available
              </option>
            </Select>
          </div>
        </div>
        <div className="row ">
          {currentUsers.map((user, index) => (
            <div key={index} className="col-md-3 mb-3">
              <Card className={styles.card} id="Card"
                style={{
                  width: 300,
                  alignContent: "center",
                  border: "0.5px solid gray",
                  cursor: "pointer",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                  }}
                >
                  <Avatar
                    size={64}
                    src={user.avatar}
                    style={{ marginBottom: 16 }}
                  />
                  <div>
                    <p>
                      <strong></strong> {`${user.first_name} ${user.last_name}`}
                    </p>
                    <p>
                      <strong>Email:</strong> {user.email}
                    </p>
                    <p>
                      <strong>Gender:</strong> {user.gender}
                    </p>
                    <p>
                      <strong>Domain:</strong> {user.domain}
                    </p>

                    {/* <p>
                      <strong>Available: </strong>{" "}
                      {user.available
                        ? true
                        : true || user.available
                        ? false
                        : false}
                      {`${user.available}`}
                    </p> */}
                  </div>
                </div>
              </Card>
            </div>
          ))}
        </div>
        <div className="d-flex justify-content-center mt-3">
          <Pagination
            current={currentPage}
            total={filteredUsers.length}
            pageSize={usersPerPage}
            onChange={handlePageChange}
          />
        </div>
      </div>
    // </div>
  );
};

export default Userdata;
